import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  wrap: {
    alignItems: "stretch",
    display: "flex",
    flexDirection: "column",
    height: "100%",
    justifyContent: "space-between",
    padding: "0 24px",

    "& .head": {
      display: "flex",
      flexDirection: "column",
      padding: "30px 0",
      rowGap: "20px",

      "& .above": {
        alignItems: "center",
        display: "flex",
        columnGap: "12px",

        "& .page-title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 700,
          lineHeight: "21px",
        },
      },

      "& .below": {
        display: "flex",
        flexDirection: "column",

        "& > small": {
          color: "#24272C",
          fontSize: "10px",
          fontWeight: 700,
          lineHeight: "12px",
          marginBottom: "6px",
          opacity: 0.5,
        },

        "& .select-business": {
          "& .MuiFormControl-root": {
            backgroundColor: "#ffffff",
            borderRadius: "8px",
            boxShadow: "0px 2px 6px rgba(0, 0, 0, 0.15)",

            "& .MuiInputBase-root": {
              "&:hover": {
                "& .MuiOutlinedInput-notchedOutline": {},
              },
              "& .MuiOutlinedInput-notchedOutline": {
                border: "none",
              },

              "& .MuiSelect-select": {
                alignItems: "center",
                columnGap: "10px",
                display: "flex",
                justifyContent: "flex-start",
                paddingTop: "8px",
                paddingLeft: "14px",
                paddingBottom: "8px",

                "&:focus": {
                  backgroundColor: "transparent",
                },

                "& .image, & .link": {
                  display: "flex",
                },

                "& .image": {
                  color: "transparent",
                },
              },
            },
          },
        },
      },
    },

    "& .menu": {
      flex: "1 0 0",
      overflow: "auto",

      "& .submenu": {
        paddingLeft: "25px",
      },
    },

    "& .bottom": {
      "& .guide-text": {
        color: "#24272cb3",
        fontSize: "12px",
        fontWeight: 400,
        lineHeight: "40px",
        margin: "7px",
      },

      "& .actions": {
        display: "flex",
        justifyContent: "space-between",
        padding: "14px 12px",

        "& .MuiLink-root": {
          cursor: "pointer",
          fontSize: "15px",
          fontWeight: 500,
          lineHeight: "18px",

          "&.payment": {
            color: "#24272C",
          },

          "&.exit": {
            color: "#AD0000",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
